/* =========================================================
 * Header Styles
========================================================= */
header {
  transition-property: 0.3s all ease;
  -moz-transition: 0.3s all ease;
  -webkit-transition: 0.3s all ease;
  -o-transition: 0.3s all ease;
  position: fixed;
  width: 100%;
  background-color: #FAFAFA;
  z-index: 10;
  border-bottom: 2px solid #E0E0E0; }

header .space {
  height: 63px; }

ul {
  padding: 1rem;
  text-decoration: none;
  list-style: none; }

li {
  padding: 0; }

nav {
  text-align: left;
  width: 100%;
  position: relative;
  height: auto;
  overflow: hidden;
  background: none; }

nav ul {
  text-align: center; }

nav ul li {
  display: inline-block; }

nav ul li a {
  display: inline-block;
  padding: 0.6em 3em 0.4em 3em;
  text-decoration: none;
  transition-property: 0.3s all ease;
  -moz-transition: 0.3s all ease;
  -webkit-transition: 0.3s all ease;
  -o-transition: 0.3s all ease; }

@media screen and (max-width: 704px) {
  header {
    height: 60px;
    background-color: #FAFAFA; } }

header.sticky {
  transition-property: 0.3s all ease;
  -moz-transition: 0.3s all ease;
  -webkit-transition: 0.3s all ease;
  -o-transition: 0.3s all ease;
  background-color: #616161; }
  header.sticky ul {
    padding-top: 0.3em;
    padding-bottom: 0.3em; }
  header.sticky nav ul li a {
    transition-property: 0.3s all ease;
    -moz-transition: 0.3s all ease;
    -webkit-transition: 0.3s all ease;
    -o-transition: 0.3s all ease;
    padding: 0.3em 3em;
    color: #FAFAFA;
    border: 1px solid #616161; }
    header.sticky nav ul li a:hover {
      background-color: #616161;
      border: 1px solid #FAFAFA;
      color: #FAFAFA; }
  header.sticky #nav-logo {
    display: none; }

@media screen and (max-width: 704px) {
  .nav-primary {
    display: none; } }

@media screen and (max-width: 768px) {
  #nav-logo {
    display: none; } }

/* =========================================================
 * Hamburger Styles
========================================================= */
.button {
  display: inline;
  position: absolute;
  right: 30px;
  top: 5px;
  z-index: 999;
  font-size: 30px; }
  @media screen and (min-width: 705px) {
    .button {
      display: none; } }

.button a {
  text-decoration: none; }

.btn-open:after {
  color: #424242;
  content: "\f0c9";
  font-family: "FontAwesome";
  transition-property: 0.3s all ease;
  -moz-transition: 0.3s all ease;
  -webkit-transition: 0.3s all ease;
  -o-transition: 0.3s all ease; }

.btn-open:hover:after {
  color: #FF6E40; }

.btn-close:after {
  color: #ffffff;
  content: "\f00d";
  font-family: "FontAwesome";
  transition-property: 0.3s all ease;
  -moz-transition: 0.3s all ease;
  -webkit-transition: 0.3s all ease;
  -o-transition: 0.3s all ease; }

.btn-close:hover:after {
  color: #FF6E40; }

/* =========================================================
 * Overlay Styles
========================================================= */
.overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: #9e9e9e;
  overflow: auto; }

.wrap {
  color: #e9e9e9;
  text-align: center;
  max-width: 90%;
  margin: 0 auto; }

.wrap ul.wrap-nav {
  border-bottom: 1px solid #FAFAFA;
  text-transform: capitalize;
  padding: 150px 0px 100px; }
  @media screen and (min-width: 640px) {
    .wrap ul.wrap-nav {
      display: none; } }

.wrap ul.wrap-nav li {
  font-size: 50px;
  display: inline-block;
  vertical-align: top;
  width: 24%;
  position: relative; }

.wrap ul.wrap-nav li a {
  font-family: "brandon-grotesque", sans-serif;
  color: #FFE57F;
  display: block;
  padding: 8px 0;
  text-decoration: none;
  transition-property: 0.3s all ease;
  -moz-transition: 0.3s all ease;
  -webkit-transition: 0.3s all ease;
  -o-transition: 0.3s all ease; }

.wrap ul.wrap-nav li a:hover {
  color: #ff9e80; }

.wrap ul.wrap-nav ul {
  padding: 20px 0; }

.wrap ul.wrap-nav ul li {
  display: block;
  font-size: 13px;
  width: 100%;
  color: #e9e9e9; }

.wrap ul.wrap-nav ul li a {
  color: #f0f0f0;
  font-family: "brandon-grotesque", serif;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 18px; }

.wrap ul.wrap-nav ul li a:hover {
  color: #ff9e80; }

